.scroll-top {
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s, visibility .3s;
    z-index: 10;
    position: fixed;
    bottom: 80px;
    right: 80px;
    max-width: 54px;
    @include until-tablet {
        max-width: 45px;
        bottom: 30px;
        left: 30px;
        right: unset; }
    & img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    &:hover {
        &::before {
            opacity: 1; }
        &::after {
            opacity: 1; } }
    &::after {}
    &::before {
        opacity: 0;
        transition: opacity .3s;
        content: ' Прокрутить наверх';
        font-size: 12px;
        padding: 2px 4px;
        border-radius: 1px;
        background-color: #B1B2B3;
        position: absolute;
        top: -20px;
        right: 0;
        width: max-content;
        @include until-tablet {
            display: none; } }

    &.active {
        opacity: 1;
        visibility: visible; } }
