.footer {
    border-top: 1px solid #EAEAEA;
    padding-bottom: 60px;
    padding-top: 20px;
    @include media-until(850px) {
        padding-bottom: 20px; }
    & .wrapper {
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        @include media-until(850px) {
            flex-wrap: wrap; }
        @include until-tablet {
            flex-direction: column;
            align-items: center; }

        & > * {
            display: flex;
            @include media-until(850px) {
                width: 100%;
                max-width: 270px; } } }

    &__left {
        flex-direction: column;
        align-items: center;
        max-width: 210px;
        @include media-until(850px) {
            width: 100%;
            max-width: unset !important;
            margin-bottom: 20px; }
        @include until-tablet {
            max-width: 270px !important; } }
    &__right {
        flex-direction: column; }
    &__social {
        margin-bottom: 15px;
        display: flex;
        flex-direction: column;
        @include until-tablet {
            align-items: center; }
        & > span {
            display: block;
            font-weight: bold;
            margin-bottom: 10px; }
        &-list {
            display: flex;
            align-items: center;
            & svg {
                width: 26px;
                height: 26px;
                fill: $black;
                transition: fill .3s;
                &:hover {
                    fill: #575756; } }
            & a {
                display: block;
                &:not(:last-of-type) {
                    margin-right: 10px; } } } }
    &__payment {
        margin-bottom: 20px;
        @include until-tablet {
            display: flex;
            flex-direction: column;
            align-items: center; }
        & > span {
            display: block;
            font-weight: bold;
            margin-bottom: 10px; }
        &-list {
            display: flex;
            align-items: center;
            justify-content: center;
            & img {
                &:not(:last-of-type) {
                    margin-right: 10px; } } } }
    &__policy {
        border-bottom: 1px dashed;
        width: max-content;
        transition: border-bottom-color .3s;
        @include until-tablet {
            margin: 0 auto; }
        &:hover {
            border-bottom-color: transparent; } }

    &__logo {
        display: block;
        max-width: 135px;
        height: 88px;
        margin-bottom: 25px;
        @include media-until(850px) {
            width: 100%;
            max-width: unset; }
        & svg {
            width: 100%;
            height: 100%; } }
    &__about {
        text-align: center; }
    &__nav {
        @include media-until(850px) {
            margin-bottom: 30px; }
        &-block {
            flex-shrink: 0;
            display: flex;
            flex-direction: column;
            &:not(:last-of-type) {
                margin-right: 50px; }
            &-title {
                font-weight: bold;
                margin-bottom: 15px; } }
        &-list {}
        &-item {
            & a {
                border-bottom: 1px dashed;
                transition: border-bottom-color .3s; }
            &:hover a {
                border-bottom-color: transparent; }
            &:not(:last-of-type) {
                margin-bottom: 10px; } } } }
