.requisites {
    margin-bottom: 80px;
    @include until-tablet {
        margin-bottom: 45px; }
    &__list {
        width: 100%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-between;
        max-height: 230px;
        @include until-tablet {
            max-height: unset; } }
    &__item {
        margin-bottom: 20px;
        display: flex;
        @include until-tablet {
            flex-direction: column; }
        &-key {
            width: 100%;
            max-width: 245px;
            display: flex;
            align-items: flex-start;
            font-size: 14px;
            line-height: 120%;
            @include until-tablet {
                margin-bottom: 5px; }
            & span {
                flex-shrink: 0; }
            &::after {
                margin: 0 3px;
                height: 13px;
                content: '';
                width: 100%;
                border-bottom: 1px dotted $black;
                @include until-tablet {
                    display: none; } } }
        &-value {
            display: inline-table;
            width: auto;
            max-width: 330px;
            font-weight: bold;
            line-height: 120%;
            @include until-tablet {
                max-width: unset; } } } }
