.banner {
    width: 100%;
    margin-bottom: 80px;
    @include until-tablet {
        margin-bottom: 45px; }
    & .swiper-container {
        width: 100%;
        &:hover {
            .swiper-buttons > * {
                visibility: visible;
                opacity: 1; } } }
    & .swiper-buttons {
        & > * {
            opacity: 0;
            visibility: hidden;
            @include until-desktop {
                opacity: 1;
                visibility: visible; } } }
    &__slide {
        width: 100%;
        position: relative;
        // padding: 70px
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 400px;
        @include until-desktop {
            height: 300px; }
        &-content {
            position: absolute;
            width: 100%;
            max-width: 330px;
            z-index: 1;
            padding: 40px 25px;
            right: 70px;
            display: flex;
            flex-direction: column;
            background: rgba(255, 255, 255, 0.9);
            border-radius: 2px;
            @include until-desktop {
                right: 40px; }
            @include until-tablet {
                align-items: center;
                right: 50%;
                transform: translateX(50%);
                @include media-until(450px) {
                    max-width: 235px;
                    padding: 30px 18px; } } }
        &-title {
            text-align: left;
            margin-bottom: 5px;
            font-family: Helvetica, sans-serif;
            @include until-tablet {
                text-align: center; } }
        &-text {
            margin-bottom: 20px;
            @include until-tablet {
                text-align: center; } }
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } }
