.catalog {
    margin-bottom: 80px;
    @include until-tablet {
        margin-bottom: 45px; }
    &__cards {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        grid-gap: 30px 50px;
        margin-bottom: 70px;
        @include until-desktop {
            grid-gap: 30px;
            margin-bottom: 40px; }
        @include until-tablet {
            grid-gap: 20px;
            grid-template-columns: repeat(auto-fill, minmax(230px, 1fr)); } }
    &__card {
        position: relative;
        overflow: hidden;
        border-radius: 2px;
        &:hover {
            .catalog__card-title span {
                border-bottom-color: transparent; }
            .catalog__card-img {
                transform: scale(1.02); } }
        &-img {
            width: 100%;
            height: 100%;
            transition: transform .3s;
            object-fit: cover; }
        &-title {
            display: flex;
            align-items: center;
            justify-content: center;
            bottom: 0;
            width: 100%;
            height: 60px;
            position: absolute;
            background: rgba(255, 255, 255, 0.8);
            border-radius: 0px 0px 2px 2px;
            @include until-tablet {
                height: 50px; }
            & span {
                transition: border-bottom-color .3s;
                text-transform: uppercase;
                padding-bottom: 3px;
                border-bottom: 1px dashed #3C3C3B;
                text-align: center;
                @include until-tablet {
                    border-bottom: 0;
                    font-size: 12px;
                    font-weight: bold; } } } } }
