.delivery {
	margin-bottom: 80px;
	@include until-tablet {
		margin-bottom: 45px; }
	& .wrapper {
		align-items: flex-start; }
	&__banner {
		width: 100%;
		padding: 20px;
		border-radius: 2px;
		background: url(../img/delivery-banner.png) no-repeat center right, linear-gradient(68.19deg, #15796D 12.07%, #52BC6A 104.57%);
		background-size: auto;
		border: 0.3px solid #3C3C3B;
		margin-bottom: 35px;
		@include until-tablet {
			background: url(../img/delivery-banner.png) no-repeat bottom, linear-gradient(68.19deg, #15796D 12.07%, #52BC6A 104.57%);
			background-size: 80%, 100%;
			padding: 10px;
			border: none; }
		@include media-until(500px) {
			background-size: 120%, 100%; }
		&-content {
			border: 0.3px solid rgba(255, 255, 255, 0.2);
			border-radius: 1px;
			padding: 70px 60px;
			@include until-tablet {
				padding: 30px;
				padding-bottom: 300px;
				display: flex;
				flex-direction: column;
				align-items: center; }
			@include media-until(500px) {
				padding: 20px;
				padding-bottom: 250px; } }
		&-text {
			max-width: 550px;
			color: white;
			margin-bottom: 25px;
			font-weight: bold;
			font-size: 50px;
			line-height: 120%;
			text-shadow: 0 0 3px black;
			@include until-tablet {
				text-align: center;
				font-size: 36px; }
			@include media-until(500px) {
				text-shadow: none; } } }
	&__info {
		display: flex;
		align-items: flex-start;
		@include until-tablet {
			width: 100%;
			flex-direction: column;
			align-items: center; }
		&-item {
			margin-right: 25px;
			display: flex;
			align-items: flex-end;
			@include until-tablet {
				flex-direction: column;
				align-items: center;
				margin-bottom: 20px;
				margin-right: 0; }
			& span {
				font-weight: bold;
				font-size: 48px;
				line-height: 1;
				color: #FFBE3C; }
			& p {
				font-size: 18px;
				margin-left: 10px;
				line-height: 30px;
				@include until-tablet {
					margin-left: 0; } } } } }
