.form {
    display: flex;
    align-items: center;
    flex-direction: column;
    & > p {
        max-width: 335px; }
    &__city {
        &-list {
            margin-top: 15px;
            width: 100%;
            columns: 2;
            column-gap: 30px;
            margin-bottom: 25px;
            & li {
                margin-bottom: 8px;
                & a {
                    font-size: 14px; } } }

        &-search {
            border: 1px solid rgba(171, 171, 171, 0.52);
            background: #FFFFFF;
            box-shadow: inset 0px 0px 10px rgba(171, 171, 171, 0.52);
            border-radius: 2px;
            height: 45px;
            appearance: none;
            width: 100%;
            display: flex;
            align-items: center;
            margin-bottom: 7px;
            padding-left: 25px;
            transition: box-shadow .3s;
            &:hover {
                box-shadow: none; }
            &:focus {
                box-shadow: none; }

            & svg {
                width: 100%;
                max-width: 17px;
                flex-shrink: 0;
                margin-right: 20px;
                fill: #B1B2B3;
                transition: fill .3s; }
            & input {
                padding-left: 5px;
                width: 100%;
                height: 100%;
                background: none;
                &:focus {
                    & + svg {
                        fill: $black; } } } } }
    &__stars {
        width: 100%;
        display: flex;
        align-items: center;
        margin-top: 10px;
        margin-bottom: 15px;
        & span {
            margin-right: 10px; }
        &-items {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            fill: #B1B2B3;
            transition: fill .3s;
            & > svg {
                &:hover {
                    fill: $orange;
                    & ~ svg {
                        fill: $orange; } } }

            & svg {
                cursor: pointer;
                width: 15px;
                height: 15px;
                &:not(:last-of-type) {
                    margin-left: 5px; } } } }
    &__title {
        font-family: Helvetica ,serif;
        text-align: center;
        margin-bottom: 7px; }
    &__text {
        margin-bottom: 15px;
        text-align: center;
        font-size: 18px;
        line-height: 130%; }
    &__error {
        margin-top: 7px;
        color: $red;
        font-size: 12px; }
    &__inputs {
        width: 100%;
        max-width: 335px;
        display: grid;
        grid-gap: 6px;
        margin-bottom: 13px;
        & label {
            & span {
                display: block;
                font-size: 14px;
                margin-bottom: 5px; }

            &.error {
                & input, textarea {
                    border-color: $red;
                    color: $red; } } }
        & textarea {
            resize: none;
            padding-top: 8px; }

        & input {
            height: 45px; }
        & input, textarea {
            background-color: #fff;
            box-shadow: inset 0px 0px 10px rgba(171, 171, 171, 0.52);
            border-radius: 2px;
            width: 100%;
            color: $black;
            padding-left: 14px;
            border: 1px solid transparent;
            transition: box-shadow .3s, border-color .3s;
            &:focus {
                box-shadow: none;
                border-color: $gray; }
            &:hover {
                border-color: $gray;
                box-shadow: none; } } }
    &__button {
        width: 100%;
        max-width: 335px;
        margin-bottom: 12px; }
    &__checkbox {
        max-width: 335px;
        width: 100%;
        display: flex;
        &.error {
            & > span::before {
                border-color: $red; } }
        & input {
            display: none;
            &:error {}
            &:checked + span {
                &::after {
                    opacity: 1; } }
            & + span {
                color: #B1B2B3;
                display: flex;
                align-items: flex-start;
                font-size: 12px;
                position: relative;
                &:hover {
                    &::before {
                        border-color: #b1b2b3; } }
                & span {
                    font-size: 12px; }
                & a {
                    font-size: 12px;
                    color: #B1B2B3;
                    border-bottom: 1px solid;
                    transition: border-color .3s;
                    &:hover {
                        border-bottom-color: transparent; } }
                &::before {
                    flex-shrink: 0;
                    content: "";
                    width: 19px;
                    height: 19px;
                    border: 1px solid;
                    margin-right: 8px;
                    transition: border-color .3s; }
                &::after {
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    content: '';
                    opacity: 0;
                    transition: opacity .3s;
                    width: 9px;
                    height: 6px;
                    border-left: 2px solid $black;
                    border-bottom: 2px solid $black;
                    transform: rotate(-45deg); } } } } }
