.contacts {
    margin-bottom: 80px;
    @include until-tablet {
        margin-bottom: 45px; }
    & .wrapper {
        align-items: stretch;
        justify-content: space-between;
        flex-direction: row;
        @include until-tablet {
            align-items: center;
            flex-direction: column; } }
    &__map {
        width: 100%;
        max-width: 460px;
        // height: 100%
        @include until-tablet {
            width: 100vw;
            height: 300px;
            max-width: unset; }
        & .map__container {
            height: 100%; } }
    &__title {
        text-align: left; }

    &__left {
        width: 100%;
        max-width: 700px;
        margin-right: 50px;
        display: flex;
        flex-direction: column;
        @include media-until(900px) {
            margin-right: 25px; }
        @include until-tablet {
            align-items: center;
            margin-right: 0;
            margin-bottom: 30px; }
        &-bottom {
            display: flex;
            @include until-tablet {
                flex-direction: column;
                align-items: center; } } }
    &__list {
        margin-bottom: 25px; }
    &__item {
        margin-bottom: 20px;
        display: flex;
        @include until-tablet {
            flex-direction: column;
            align-items: center;
            margin-bottom: 15px; }
        &-key {
            width: 100%;
            max-width: 245px;
            display: flex;
            align-items: flex-start;
            font-size: 14px;
            line-height: 120%;
            @include until-tablet {
                width: max-content;
                margin-bottom: 5px; }
            & span {
                flex-shrink: 0; }
            &::after {
                margin: 0 3px;
                height: 13px;
                content: '';
                width: 100%;
                border-bottom: 1px dotted $black;
                @include until-tablet {
                    display: none; } } }
        &-value {
            display: inline-table;
            width: auto;
            flex-shrink: 0;
            max-width: 330px;
            font-weight: bold;
            line-height: 120%; } }
    &__social {
        margin-right: 20px;
        display: flex;
        flex-direction: column;
        @include until-tablet {
            margin-right: 0;
            margin-bottom: 20px; }

        &-title {
            margin-bottom: 5px;
            font-weight: bold; }
        &-list {
            display: flex;
            align-items: center; }

        & a {
            display: block;
            width: 20px;
            height: 20px;
            @include until-tablet {
                width: 30px;
                height: 30px; }
            &:not(:last-of-type) {
                margin-right: 10px; }
            &:hover svg {
                fill: #575756; }
            & svg {
                transition: fill .3s;
                width: 100%;
                height: 100%;
                fill: $black; } } } }
