.office {
    margin-bottom: 80px;
    @include until-tablet {
        margin-bottom: 45px; }
    & .wrapper {
        @include media-until(380px) {
            padding: 0; } }
    & .swiper-buttons {
        opacity: 0;
        visibility: hidden;
        @include media-until(1200px) {
            opacity: 1;
            visibility: visible; } }
    &__cards {
        width: 100%;
        & .swiper-wrapper {
            @include media-from(1200px) {
                width: 100%;
                display: grid;
                grid-template-columns: repeat(auto-fill, minmax(285px,1fr));
                grid-gap: 30px; } } }

    &__card {
        width: 100%;
        max-width: 320px;
        @include media-until(1200px) {
            margin-right: 20px; }
        @include media-until(380px) {
            width: 100vw;
            max-width: unset; }
        & img {
            width: 100%; } } }
